"use client";

import { useCallback, useEffect, useRef, useState } from "react";
import CharacterTopBar from "@/components/workspace/character/CharacterTopBar";
import {
  listCharacters,
  getAsset,
  upsertAsset,
  deleteAsset,
  type CharacterAssetRecord,
} from "@/components/workspace/character/db";

export default function CharacterMeshView() {
  const [activeCharacterId, setActiveCharacterId] = useState<string | null>(null);
  const [asset, setAsset] = useState<CharacterAssetRecord | null>(null);
  const [status, setStatus] = useState<string>("No character selected");
  const [err, setErr] = useState<string | null>(null);

  const fileInputRef = useRef<HTMLInputElement | null>(null);

  const loadCharacters = useCallback(async () => {
    const all = await listCharacters();
    return all.map((c) => ({ id: c.id, name: c.name }));
  }, []);

  const loadAssetForCharacter = useCallback(async (characterId: string | null) => {
    setErr(null);
    setAsset(null);
    if (!characterId) {
      setStatus("No character selected");
      return;
    }

    setStatus("Checking saved mesh…");
    try {
      const rec = await getAsset(characterId, "blockout_glb");
      if (rec) {
        setAsset(rec);
        setStatus(`Mesh saved: ${rec.filename}`);
      } else {
        setStatus("No saved mesh yet");
      }
    } catch (e: any) {
      setErr(String(e?.message ?? e));
      setStatus("Could not read saved mesh");
    }
  }, []);

  useEffect(() => {
    void loadAssetForCharacter(activeCharacterId);
  }, [activeCharacterId, loadAssetForCharacter]);

  async function onCharacterSelected(id: string | null) {
    setActiveCharacterId(id);
  }

  function pickGlb() {
    if (!activeCharacterId) return;
    fileInputRef.current?.click();
  }

  async function onGlbSelected(e: React.ChangeEvent<HTMLInputElement>) {
    const file = e.target.files?.[0];
    e.target.value = "";
    if (!file || !activeCharacterId) return;

    setErr(null);
    const now = new Date().toISOString();
    try {
      await upsertAsset({
        characterId: activeCharacterId,
        type: "blockout_glb",
        filename: file.name || "character-blockout.glb",
        blob: file,
        updatedAt: now,
      });
      setStatus("Mesh saved");
      await loadAssetForCharacter(activeCharacterId);
    } catch (e: any) {
      setErr(String(e?.message ?? e));
    }
  }

  function downloadGlb() {
    if (!asset) return;
    const url = URL.createObjectURL(asset.blob);
    const a = document.createElement("a");
    a.href = url;
    a.download = asset.filename || "character-blockout.glb";
    document.body.appendChild(a);
    a.click();
    a.remove();
    URL.revokeObjectURL(url);
  }

  async function clearGlb() {
    if (!activeCharacterId) return;
    setErr(null);
    try {
      await deleteAsset(activeCharacterId, "blockout_glb");
      setAsset(null);
      setStatus("Mesh removed");
    } catch (e: any) {
      setErr(String(e?.message ?? e));
    }
  }

  return (
    <div className="h-full w-full overflow-hidden rounded-2xl border border-[rgb(var(--border))] bg-[rgb(var(--panel))]">
      <CharacterTopBar
        title="Mesh Workspace"
        loadCharacters={loadCharacters}
        onCharacterSelected={onCharacterSelected}
        rightSlot={
          activeCharacterId ? (
            <div className="text-xs text-[rgb(var(--muted))]">{status}</div>
          ) : (
            <div className="text-xs text-[rgb(var(--muted))]">No character selected</div>
          )
        }
      />

      <div className="p-6">
        <input
          ref={fileInputRef}
          type="file"
          accept=".glb,model/gltf-binary"
          className="hidden"
          onChange={onGlbSelected}
        />

        <div className="rounded-2xl border border-[rgb(var(--border))] bg-[rgb(var(--bg))] p-5">
          <div className="flex flex-wrap items-center justify-between gap-3">
            <div>
              <div className="text-sm font-semibold text-[rgb(var(--text))]">Saved mesh asset</div>
              <div className="mt-1 text-xs text-[rgb(var(--muted))]">
                {activeCharacterId
                  ? asset
                    ? `blockout_glb: ${asset.filename}`
                    : "No blockout_glb saved yet"
                  : "Select a character to attach a mesh"}
              </div>
            </div>

            <div className="flex items-center gap-2">
              <button
                type="button"
                onClick={pickGlb}
                disabled={!activeCharacterId}
                className="rounded-xl border border-[rgb(var(--border))] bg-[rgb(var(--panel))] px-3 py-2 text-sm text-[rgb(var(--text))] disabled:opacity-50"
                title={activeCharacterId ? "Upload a GLB and attach to this character" : "Select a character first"}
              >
                Upload GLB
              </button>

              <button
                type="button"
                onClick={downloadGlb}
                disabled={!asset}
                className="rounded-xl border border-[rgb(var(--border))] bg-[rgb(var(--panel))] px-3 py-2 text-sm text-[rgb(var(--text))] disabled:opacity-50"
              >
                Download GLB
              </button>

              <button
                type="button"
                onClick={clearGlb}
                disabled={!asset || !activeCharacterId}
                className="rounded-xl border border-[rgb(var(--border))] bg-[rgb(var(--panel))] px-3 py-2 text-sm text-[rgb(var(--text))] disabled:opacity-50"
                title="Remove saved mesh for this character"
              >
                Remove
              </button>
            </div>
          </div>

          {err && (
            <div className="mt-4 rounded-xl border border-red-500/30 bg-red-500/10 p-3 text-sm text-red-700 dark:text-red-200">
              {err}
            </div>
          )}

          <div className="mt-5 rounded-2xl border border-dashed border-[rgb(var(--border))] bg-[rgb(var(--panel))] p-10 text-center text-sm text-[rgb(var(--muted))]">
            Mesh editor placeholder.
            <div className="mt-1 text-xs">
              When your mesh workspace exports a GLB, call <code>upsertAsset</code> with type <code>"blockout_glb"</code>.
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
